
import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Art from 'react-color-wander';
import styled, { keyframes } from 'styled-components';
import { invert } from 'color-invert';
import ReactPlayer from 'react-player'

import maps from './maps';
import palettes from './palettes';


const getRandom = () => ({
  map: maps[Math.floor(Math.random() * maps.length)],
  palette: palettes[Math.floor(Math.random() * palettes.length)]
});


class App extends Component {

  state = {
    custom: false,
    more: false,
    map: getRandom().map,
    mounted: false,
    palette: getRandom().palette, // react colors
    stopped: false
  };
  
  drawArt = () => {

     // just a trick for a smooth transition when re-drawing
    if (this.art.metadata().palette)
      document.body.style.background = this.art.metadata().palette[0];

    this.art.draw();
  };



  randomize = () => this.setState(getRandom(), () => this.drawArt());

  createRef = ref => {
    this.art = ref;

    if (!this.state.mounted)
      this.setState({ mounted: true }, () => this.drawArt());
  };

  renderArt = () => {
    return (
        <Art
          ref={this.createRef}
          map={this.state.map}
          palette={this.state.palette}
          seed="259025"
          // height={600} // default = innerHeight
          // width={800} // default = innerWidth
        />
    );
  };


render() {
  return (
    <div className="App">
      
      <ContentContainer onClick={this.randomize}
      style={{
        color: invert(this.state.palette[1])
      }}>
      <Title>
        LBC
      </Title>
      <SubTitle>
        lundabeachclub 2lax19
      </SubTitle>
      </ContentContainer>
       
      {this.renderArt()}

      <VideoContainer>
        <ReactPlayer url='https://www.youtube.com/watch?v=AtXkC0lXl98' playing />
      </VideoContainer>
      
  
    </div>
  );
}
}



const fadeIn = keyframes`
0% {
  opacity: 0;
}
100% {
  opacity:
}
`

const ContentContainer = styled.div`
  z-index: 999;
  position: absolute;
  text-align: center;
  width: 100%;
  height: 100%;
  justify-content: center;
  animation: ${fadeIn} 1s ease-in-out 0.1s;
`;

const VideoContainer = styled.div`
  opacity: 0.00001;
`;



const Title = styled.h1`
  padding-top: 20vh;
  font-size: 16vw;
  text-align: center;
  font-family: 'Nosifer', sans-serif;
  margin: 0;
  line-height: 1;
`;

const SubTitle = styled.p`
  font-size: 4vw;
  text-align: center;
  font-family: 'Martel Sans', sans-serif;
  margin: 0;
  font-weight: 800;
`;




 const keyFrameExampleOne = keyframes`
  0% {
    height: 200px;
  }
  100% {
    height: 600px;
    background: orange;
  }
`

export default App;
